//vendor
import React, { useContext } from "react";
import { useForm } from "../../../../hooks/useForm";
//react-router
//stlyes
import "../../../../styles/screen/login-screen/loginleftside.css";
import { UserContext } from "../../../../useContext/UserContext";

export const LoginLeftSide = ({ history }) => {
  const [formLogin, handleInputChange] = useForm({
    emailLogin: "heliosoto17@gmail.com",
    passwordLogin: "123456",
  });

  const { setUser } = useContext(UserContext);

  const { emailLogin, passwordLogin } = formLogin;
  const handleLogin = async (e) => {
    try {
      e.preventDefault();
      const rawResponse = await fetch(
        " https://api-fc.herokuapp.com/authAPI/login",
        {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            email: emailLogin,
            password: passwordLogin,
          }),
        }
      );
      const content = await rawResponse.json();
      history.push("/table");
      await setUser(content);
    } catch (error) {
      alert("El email o password es invalido");
    }
  };
  return (
    <div className="loginleftcomponent">
      <h2 className="loginleftcomponent__title">
        OpenBootcamp
        <span className="loginleftcomponent__title--span"> | Alumnos</span>
      </h2>
      <form onSubmit={handleLogin} className="loginleftcomponent__form">
        <div className="loginleftcomponentform__box">
          <label className="loginleftcomponent__form--label">Email</label>
          <input
            onChange={handleInputChange}
            className="loginleftcomponentform__box--input"
            type="text"
            placeholder=" Introduce tu correo"
            id="email"
            name="emailLogin"
            value={emailLogin}
          />
        </div>
        <div className="loginleftcomponentform__box">
          <label className="loginleftcomponentform__box--label">
            Constraseña
          </label>
          <input
            onChange={handleInputChange}
            className="loginleftcomponentform__box--input"
            type="password"
            placeholder=" Introduce tu contraseña"
            id="password"
            name="passwordLogin"
            value={passwordLogin}
          />
        </div>
        <div>
          <div className="loginleftcomponent__spanbox">
            <span>
              <input type="checkbox" /> Recuerdame
            </span>
            <span> He olvidado la contraseña</span>
          </div>
        </div>
        <button className="loginleftcomponent__btn" type="submit">
          {" "}
          Iniciar Sesión{" "}
        </button>
      </form>
      <footer className="loginleftcomponent__footer">
        <div>Copyright © 2021 Open Bootcamp SL, Imagina Group</div>
        <div>Todos los derechos reservados.</div>
        <div>Política de Privacidad</div>
        <div>Tqm guido</div>
      </footer>
    </div>
  );
};
